import React, { Component } from 'react';

class ScrollWrapper extends Component {
	constructor(props) {
	    super(props);
	    this.state = {
	    	screenHeight: 0,
	    	componentOffsetTop: 0,
	    	isInView: false
	    }
	    this.myRef = React.createRef();
  	}
	componentDidMount() {
		// console.log(this.myRef.current.offsetTop);
		this.setState({
			...this.state,
			componentOffsetTop: this.myRef.current.offsetTop,
			screenHeight: window.screen.height
		}, () => {
			window.onscroll = function() {
				let val = this.state.componentOffsetTop - this.state.screenHeight
				if(document.body.scrollTop > val || document.documentElement.scrollTop > val ) {
					// console.log('success');
				} else {

				}
			  	// console.log(this.state.componentOffsetTop - this.state.screenHeight +" ::: "+ document.documentElement.scrollTop);
			}.bind(this);
		})
	}
  	render() {
    	return (
      		<section ref={this.myRef}>
      		</section>
    	);
  	}
}

export default ScrollWrapper;



